#header-title {
  padding: 10px 10px 0 10px; }
  #header-title .logo-wrap .logo {
    background-image: none;
    color: white;
    font-size: 3em;
    text-decoration: none;
    font-family: Source Code Pro, monospace; }
  #header-title .subtitle-wrap {
    float: right; }
  #header-title img {
    float: left;
    border-radius: 50%;
    margin: 0 20px 10px 10px; }

.figure .caption {
  display: none; }

main div a {
  text-decoration: underline; }

#avatar {
  border-radius: 64px;
  border: 1px solid #9a9ea3;
  height: 128px;
  width: 128px; }

.credit .logo-wrap a {
  font-size: 3em;
  font-family: Source Code Pro, monospace; }

.credit .logo-wrap .logo {
  background: none; }

img.intro {
  opacity: 0.2;
  margin: 16vh 0 0 -22vh;
  position: absolute;
  height: 45vh;
  width: 45vh; }

