#page-resume #contact-info {
  display: none; }

#page-resume h1 {
  font-size: 1.5em; }

#page-resume h2 {
  font-size: 1.3em; }

#page-resume h3 {
  font-size: 1.1em; }

#page-resume span.keywords {
  font-size: smaller;
  margin-left: 1em; }

#page-resume dl dt {
  display: list-item;
  list-style-image: url("/images/bullet.gif");
  margin-left: 0;
  margin-right: 8px; }

#page-resume table {
  margin: 1em 0 1em 0;
  width: initial; }
  #page-resume table td {
    text-align: left;
    margin: 0;
    padding: 0;
    border: none;
    padding-right: 0.5em; }
    #page-resume table td i {
      font-size: 1.4em; }
  #page-resume table img {
    border-radius: 50%;
    margin-right: 1em; }

#page-resume .caption {
  display: none; }

#page-resume li {
  list-style-image: url("/images/bullet.gif"); }

#page-resume .well {
  background-color: #fefefe;
  padding: 0.7em; }

