#projects .tabs {
  padding: 0 0 0 2em;
  margin: 0; }
  #projects .tabs li {
    list-style-type: none;
    display: inline-block;
    padding: 0.4em;
    margin-left: 0.4em;
    border-bottom: none;
    background-color: #f0f0f0;
    border-top-left-radius: 0.5em;
    border-top-right-radius: 0.5em; }
  #projects .tabs .active {
    background-color: #fff; }
    #projects .tabs .active a {
      font-weight: bold; }

#projects #open-source .panel-heading, #projects #closed-source .panel-heading {
  display: none; }

#projects #open-source .list-group-item, #projects #closed-source .list-group-item {
  list-style-type: none;
  margin-bottom: 0.4em; }
  #projects #open-source .list-group-item a, #projects #closed-source .list-group-item a {
    font-size: 1.5em; }

#projects .tab-content {
  background: #fff;
  padding: 0.25em 0.5em 0 0.5em;
  margin: 0;
  position: relative;
  top: 0;
  left: 0; }
  #projects .tab-content .tab-pane {
    display: none; }
  #projects .tab-content .active {
    display: block; }

